import React, { Component } from 'react';
import styled, { keyframes } from 'styled-components';
import { getAds, getAdsByTags } from '../api/FileSystem';
import { format, render, cancel, register } from 'timeago.js';
import norwegian from './norwegian';

register('NB-no', norwegian);

const fadeIn = keyframes`
  from {
    opacity: 0;
  }

  to {
    opacity: 1;
  }
`;


const PageWrapper = styled.div`
  position: relative;
  display: flex;

  .close-block {
    position: fixed;
    left: 0;
    top: 0;
    width: calc(100% - 240px);
    height: 100%;
    z-index: 10001;
    display: none;
    &.opened {
      display: block;
    } 
  }
`;

const ExpandButton = styled.button`
  position: relative;
  width: 100%;
  text-transform: uppercase;
  color: white;
  z-index: 400;
  padding: 17px;
  margin-bottom: 20px;
  border-radius: 2px;
  background: linear-gradient(45deg, #a51ac2, red);
  transition: all 0.1s linear;
  &.opened {
    opacity: 0.6;
  }
  &:hover {
    background: linear-gradient(45deg, #a51ac2, red);
  }
  @media (min-width: 700px) {
    display: none;
  }
  &:after {
    font-family: "Helveticons";
    content: "\\e796";
    font-size: 30px;
    display: inline-block;
    position: absolute;
    right: 10px;
    top: 4px;
  }
`;

const MenuWrapper = styled.div`

  position: fixed;
  top: 0;
  right: 0;
  width: 100%;
  height: 100%;
  background: black;
  z-index: 1000;
  overflow: auto;
  webkit-overflow-scrolling: touch;
  width: 240px;
  flex: 0 0;
  box-shadow: 0 0 23px rgba(0,0,0,0.8);

  padding: 20px;
  display: flex;
  justify-content: center;
  align-items: flex-start;
  transform: translateX(100%);
  transition: all 0.1s linear;
  &.opened {
    transform: translateX(0);
  }
  

  @media (min-width: 700px) {
    position: relative;
    width: auto;
    height: auto;
    background: transparent;
    width: 300px;
    transform: translateX(0);
    flex: 0 1 300px;
  }


  
  .listing-search {
    input {
      background: transparent;
      border: 1px solid rgba(255,255,255,0.4);
      padding: 4px;
      border-radius: 2px;
      font-size: 16px;
      color: white;
      &:focus {
        border: 1px solid rgba(255,255,255,0.8);
      }
    }
  }



  h4 {
    margin-bottom: 10px;
    font-size: 20px;
    margin-top: 26px;
  }
  ul {
    list-style: none;
    margin: 0;
    padding: 0;
    li {
      margin-bottom: 4px;
      display: flex;
      cursor: pointer;
      &.disabled {
        opacity: 0.6;
      }
      &:hover {
        background: rgba(255,255,255,0.1);
      }
      input {
        margin-right: 6px;
        margin-bottom: 0;
        margin-top: 6px;
      }
      label {
        display: flex;
        width: 100%;
        
      }
      .property-name {
        display: block;
        font-weight: normal;
        &::first-letter {
          text-transform: uppercase;
        }
        margin-right: 6px;
      }
    }
  }
`;

const ContentListing = styled.div`
  padding: 0;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: flex-start;
  width: 100%;
  @media (min-width: 700px) {
    padding: 20px;
  }
  .listings {
    list-style: none;
    padding: 0;
    margin: 0;
    width: 100%;
    li {
      display: block;
      animation: ${fadeIn} 0.5s ease-in-out;
    }
    a {
      padding: 10px;
      border: 1px solid rgba(255,255,255,0.1);
      display: flex;
      width: 100%;
      align-items: stretch;
      margin-bottom: 10px;
      pointer: cursor;

      &:hover {
        border: 1px solid rgba(255,255,255,0.4);
      }
    }
    p {
      display: none;
      @media (min-width: 700px) {
        display: block;
      }
    }
    a {
      color: white;
      text-decoration: none;
    }
    h2,p {
      margin: 0;
      padding: 0;
    }
    h2 {
      margin: 0;
      padding: 0;
      font-weight: normal;
      letter-spacing: 0;
      font-size: 14px;
      margin-bottom: 4px;
      @media (min-width: 700px) {
        font-size: 19px;
        letter-spacing: 1px;
      }
    }
    p {
      font-size: 12px;
      opacity: 0.8;
      padding-bottom: 1px;
      display: none;
      @media (min-width: 700px) {
        font-size: 15px;
        display: block;
      }
    }
    .premium {
      position: relative;
      &:before {
        position: absolute;
        content: "";
        left: 0;
        top: 0;
        width: 2px;
        height: 100%;
        background: linear-gradient(110deg, #a51ac2, #ff8d00);
      }
      
      
    }
    .time {
      text-transform: uppercase;
      font-size: 10px;
      margin-bottom: 2px;
      display: block;
      color: #ff00dc;
      text-align: left;
    }
    .city-listing {
      display: flex;
      font-size: 12px;
      margin-bottom: 2px;
      font-weight: bold;
      opacity: 0.4;
    }
    .city-name {
      display: block;
      margin-left: 6px;
      font-weight: normal;
      &::first-letter {
          text-transform: uppercase;
      }
    }
    .city-container {
      display: flex;
      
    }
  }
  .listing-company-image {
    width: 70px;
    height: 70px;
    position: relative;
    margin-right: 10px;
    flex: 0 0 70px;
    figure {
      position: absolute;
      left: 0;
      top: 0;
      width: 100%;
      height: 100%;
      background-size: cover !important;
      margin: 0;
    }
    @media (min-width: 700px) {
      width: 100px;
      height: 100px;
      flex: 0 0 100px;
    }
  }
`;

let listingCache = [];
let listingByTagsCache = {};

class Master extends Component {

  state = {
    ads: [],
    adsByTags: {
      technology: [],
      location: [],
      type: []
    },
    filters: {
      technology: [],
      location: [],
      type: [],
      excludeIds: []
    },
    search:"",
    filterIsOpen: false
  };
  /**
   * This functions needs to be broken up. I just don't have time right now.
   * It receives a filterword (IE "Java"), that the user has clicked in the left menu.
   * Then it tries to either add of remove it from the state.filters arrays.
   * 
   * Then it cycles through all ads and removes the ones that don't have the corresponding filter tag.
   * Then it collects the ids of all the ads that don't have the filter tags.
   * then it removes them from the object populating the left menu.
   * 
   * @param {*} category 
   * @param {*} name 
   */
  updateFilter (category, name, phrase) {
    let filters = this.state.filters;
    let adsByTags = JSON.parse(JSON.stringify(listingByTagsCache)); // unlinked version of left menu listings
    let ads = listingCache.slice(); // unlinked version of ads list
    let excludeIds = []; // the list of ids to exclude from the left menu
  
    this.setState({search: phrase});


    if(name !== "" && category !== "") {
      if(filters[category].indexOf(name) > -1) {
        filters[category].splice(filters[category].indexOf(name), 1);  
      } else {
        filters[category].push(name);
      }
    }
    if(phrase) {
      ads = ads.filter(ad => {
        let titleMatch = ad.title.toLowerCase().indexOf(phrase.toLowerCase());
        let subTitleMatch = ad.subTitle.toLowerCase().indexOf(phrase.toLowerCase());
        let bodyTextMatch = ad.bodyText.toLowerCase().indexOf(phrase.toLowerCase());
        if(titleMatch > -1 || subTitleMatch > -1 || bodyTextMatch > -1) {
          return true;
        } else {
          if(excludeIds.indexOf(ad.id <= -1)) {
            excludeIds.push(ad.id);
          }
          return false;
        }
      });
    }
    
    if(filters.technology.length) {
      ads = ads.filter((ad,key) => {

        let matches = ad.technology.filter(tech => filters.technology.indexOf(tech) > -1);

        if(matches.length && matches.length === filters.technology.length) {
          return ad;
        } else {
          if(excludeIds.indexOf(ad.id <= -1)) {
            excludeIds.push(ad.id);
          }
          return false;
        }
      })
    }

    if(filters.location.length) {
      ads = ads.filter(ad => {

        let matches = ad.location.filter(tech => filters.location.indexOf(tech) > -1);
        if(matches.length && matches.length === filters.location.length) {
          return ad;
        } else {
          if(excludeIds.indexOf(ad.id <= -1)) {
            excludeIds.push(ad.id);
          }
          return false;
        }
      })
    }

    if(filters.type.length) {
      ads = ads.filter(ad => {

        let matches = ad.jobType.filter(tech => filters.type.indexOf(tech) > -1);
        if(matches.length && matches.length === filters.type.length) {
          return ad;
        } else {
          if(excludeIds.indexOf(ad.id <= -1)) {
            excludeIds.push(ad.id);
          }
          return false;
        }
      })
    }
    
    if(excludeIds.length) {
     
      for(let prop in adsByTags.technology){
        adsByTags.technology[prop] = adsByTags.technology[prop].filter(adId => excludeIds.indexOf(adId) <= -1);
      }
      for(let prop in adsByTags.location){
        adsByTags.location[prop] = adsByTags.location[prop].filter(adId => excludeIds.indexOf(adId) <= -1);
      }

      for(let prop in adsByTags.type){
        adsByTags.type[prop] = adsByTags.type[prop].filter(adId => excludeIds.indexOf(adId) <= -1);
      }
    }

    this.setState({
      ads: ads,
      adsByTags: adsByTags
    })

  }
  
  componentDidMount () {
    getAds((ads) => {
      listingCache = ads;
      this.setState({
        ads: listingCache.slice()
      })
    })
    getAdsByTags((tagCategories) => {
      listingByTagsCache = tagCategories;
      this.setState({
        adsByTags: JSON.parse(JSON.stringify(listingByTagsCache))
      })
    })
  }

  toggleFilter () {
    this.setState({
      filterIsOpen: !this.state.filterIsOpen
    })
  }

  render () {
    let ads = this.state.ads; 
    let adsByTags = this.state.adsByTags;
    let phrase = this.state.phrase;
    ads.map(ad => ad.publishedAgo = format(ad.published, 'NB-no'));

    return (
      <PageWrapper>
        <div className={`close-block ${this.state.filterIsOpen ? "opened": ""}`} onClick={() => this.toggleFilter()}></div>
        
        <MenuWrapper className={this.state.filterIsOpen ? "opened": ""}>
          
          <div>
            <div className="listing-info">
                <h3>Filter</h3>
            </div>
            <div className="listing-search">
              <input type="text" value={this.state.search} onChange={(event) => this.updateFilter("", "", event.target.value)} placeholder="Sk.. f.eks. Node" />
            </div>
            <h4>Teknologi</h4>
            <ul>
              {Object.keys(adsByTags.technology).sort().map((key, index) => (
                <li className={adsByTags.technology[key].length === 0 ? "disabled": ""} key={key}><input id={`${key}-${index+1}`} disabled={adsByTags.technology[key].length === 0} type="checkbox" onClick={() => this.updateFilter("technology", key, phrase)}/><label htmlFor={`${key}-${index+1}`}><span className="property-name">{key}</span> ({adsByTags.technology[key].length})</label></li>      
              ))}
            </ul>
            <h4>Sted</h4>
            <ul>
              {Object.keys(adsByTags.location).sort().map((key, index) => (
                <li className={adsByTags.location[key].length === 0 ? "disabled": ""} key={key}><input id={`${key}-${index+1}`} disabled={adsByTags.location[key].length === 0} type="checkbox" onClick={() => this.updateFilter("location", key, phrase)} /><label htmlFor={`${key}-${index+1}`}><span className="property-name">{key}</span> ({adsByTags.location[key].length})</label></li>      
              ))}
            </ul>
            <h4>Stilling</h4>
            <ul>
              {Object.keys(adsByTags.type).sort().map((key, index) => (
                <li className={adsByTags.type[key].length === 0 ? "disabled": ""} key={key}><input id={`${key}-${index+1}`} disabled={adsByTags.type[key].length === 0} type="checkbox" onClick={() => this.updateFilter("type", key, phrase)} /><label htmlFor={`${key}-${index+1}`}><span className="property-name">{key}</span> ({adsByTags.type[key].length})</label></li>      
              ))}
            </ul>
          </div>

        </MenuWrapper>
        <ContentListing>
              <div className="listing-info">
                <h3>{ads.length} ledige utviklerstillinger</h3>
              </div>
              <ExpandButton className={this.state.filterIsOpen ? "opened": ""} onClick={() => this.toggleFilter()}>pne filter</ExpandButton>
                <ul className="listings">
                {ads.map((ad, key) => (
                  <li key={key} className={ad.premium.length ? "premium" : ""}>
                    <a target="_blank" href={"https://kode24.no" + ad.url}>
                      <div className="listing-company-image">
                        <figure style={{background: `url(${ad.company.imageUrl})`}}></figure>
                      </div>
                      <div className="listing-info">
                        <time className="time">{ad.publishedAgo}</time>
                        <p>{ad.company.company}</p>
                        <h2>{ad.title}</h2>
                        {ad.location.length > 0 && (
                          <p className="city-listing">
                            Sted:
                            {ad.location.map((key, index) => (
                              <span key={key} className="city-container">
                                {index > 0 ? ", ": ""}
                                <span className="city-name">
                                  {key}
                                </span>
                              </span>      
                            ))}
                          </p>
                        )}  

                        {ad.jobType.length > 0 && (
                          <p className="city-listing">
                            Jobbtype:
                            {ad.jobType.map((key, index) => (
                              <span key={key} className="city-container">
                                {index > 0 ? ", ": ""}
                                <span className="city-name">
                                  {key}
                                </span>
                              </span>      
                            ))}
                          </p>
                        )}

                        {ad.technology.length > 0 && (
                          <p className="city-listing">
                            Teknologi:
                            {ad.technology.map((key, index) => (
                              <span key={key} className="city-container">
                                {index > 0 ? ", ": ""}
                                <span className="city-name">
                                  {key}
                                </span>
                              </span>      
                            ))}
                          </p>
                        )}

                      </div>
                    </a>  
                  </li>
                ))}
                </ul>
        </ContentListing>
      </PageWrapper>
      
    )
  }

}

export default Master;


