
const getAdsByTags = (success, fail) => {
fetch('https://kode24-joblisting.herokuapp.com/api/listing/bytags', {
    method: 'get'
 })
 .then(response => {
    if (response.ok) {
        return response.json();
    } else {
        console.log(response);
        throw new Error('401');
    }
  })
.then(response => {
    success(response);
})
.catch(error => {
    console.log('error', error);
});
}

const getAds = (success, fail) => {
    fetch('https://kode24-joblisting.herokuapp.com/api/listing', {
        method: 'get'
     })
     .then(response => {
        if (response.ok) {
            return response.json();
        } else {
            console.log(response);
            throw new Error('401');
        }
      })
    .then(response => {
        success(response);
    })
    .catch(error => {
        console.log('error', error);
    });
    }


export { getAds, getAdsByTags };


